<template>
    <div>
        <span
            v-if='item.authorName'
            class="item-author">
            {{ item.authorName }}
        </span>
        <span
            v-if='item.commentCount'
            class="item-commentCount">
            {{ item.commentCount }}条评论
        </span>
        <span
            v-if="item.publishTime"
            class="item-publishtime">
            {{ computeTimeAgo(item.publishTime) }}
        </span>
    </div>
</template>

<script>
import { ago } from '@/utils/time';

export default {
    name: 'ItemInfo',
    props: {
        item: {
            type: Object,
            required: true,
        },
    },
    methods: {
        computeTimeAgo(time) {
            return ago(time, 'year', 'YYYY年MM月DD日');
        },
    },
};
</script>

