<template>
    <div class="share">
        <div
            @click="clickMoreEpidemicTitle"
            class="suspend-title">
            更多疫情 <span />
        </div>
        <div class="banner"/>
        <div class="pneumonia">
            <Current :data="yiQingData" />
            <div class="interval"/>
            <ConvMap
                :data='otherLink'
                @goAppStore='goAppStore'/>
            <div class="fix-banner">
                <div
                    @click="getMoreReport"
                    class="more-report">
                <span/>查看更多肺炎报道</div>
                <div
                    v-if='!xiaomiShequ'
                    @click="shareCurrent"
                    class="share-epidemic">
                    <div class="compassion"/>
                </div>
            </div>
            <div class="interval"/>
            <div class="focus">
                <div class="focus-title">
                    焦点关注
                    <span
                        @click="getMoreReportFocus"
                        class="more-report-focus">
                        更多报道 <span/>
                    </span>
                </div>
                <div class="focus-list">
                    <Item
                        v-for="(item,index) in list"
                        :pathname="pathname"
                        :xiaomi-shequ='xiaomiShequ'
                        :o2o-channel='o2oChannel'
                        @goAppStore='goAppStore'
                        :item="computeParams(item)"
                        :key="index"/>
                </div>
                <div
                    @click="clickMoreEpidemicBanner"
                    class="penumonia-focus-banner">
                    了解更多疫情报道 前往想看
                    <span />
                </div>
            </div>
        </div>
        <ShareGuideMask
            :show-guide-mask = 'showGuideMask'
            @closeGuideMask="closeGuideMask"/>
    </div>
</template>

<script>
import { getTabLink } from '@/utils/jsApiV2/deeplink';
import { setShareInfo, deeplinkApp } from '@/utils/share';
import { getFightConvInfo, getNewsList } from '@/api/pneumoniaShare';
import Current from './Current';
import ConvMap from './ConvMap';
import Item from './Item';
import ShareGuideMask from './ShareGuideMask';
import shareChannel from './config';

export default {
    data() {
        return {
            list: [],
            yiQingData: {},
            otherLink: {},
            showGuideMask: false,
            pathname: window.location.pathname,
            xiaomiShequ: '',
            o2oChannel: '',
        };
    },
    created() {
        document.title = '实时动态 | 新型肺炎疫情 - 想看';
        this.xiaomiShequ = shareChannel.call(this).xiaomiShequ;
        this.o2oChannel = shareChannel.call(this).o2oChannel;
        this.getFightConvInfo();
        this.getMoreEpidemic();
        this.setShareInfo();
    },
    components: {
        Current,
        ConvMap,
        Item,
        ShareGuideMask,
    },
    methods: {
        getMoreReportFocus() {
            this.$o2o({
                e_c: `${this.o2oChannel}肺炎频道`,
                e_a: '点击',
                e_n: `${this.o2oChannel}更多报道点击`,
            });
            this.goAppStore();
        },
        getFightConvInfo() {
            getFightConvInfo().then((res) => {
                this.$root.hidePlaceholder();
                this.yiQingData = res.yiQingData;
                this.otherLink = res.otherLink;
                this.$o2o({
                    e_c: `${this.o2oChannel}肺炎频道`,
                    e_a: '曝光',
                    e_n: `${this.o2oChannel}肺炎页面曝光`,
                });
            }).catch(() => {
                this.$error();
            });
        },
        getMoreEpidemic() {
            /*
                offset int类型 0为下拉 -1位上滑
            */
            getNewsList({
                channelId: '抗击肺炎',
                offset: 0,
            }).then((res) => {
                // 筛选数据,确保数据源
                const itmStyleType = ['0', '1', '3', 'inline_video_item', 'short_video_item'];
                // eslint-disable-next-line max-len
                this.list = res[0].documents.filter(item => itmStyleType.includes(item.item_style));
            }).catch(() => {
                this.list = [];
            });
        },
        shareCurrent() {
            this.showGuideMask = true;
            this.$o2o({
                e_c: `${this.o2oChannel}肺炎频道`,
                e_a: '点击',
                e_n: `${this.o2oChannel}分享疫情按钮点击`,
            });
        },
        closeGuideMask() {
            this.showGuideMask = false;
        },
        setShareInfo() {
            setShareInfo({
                title: '实时动态 | 新型肺炎疫情 - 想看',
                summary: '实时新型冠状病毒肺炎疫情，众志成城，全民加油！#想看#',
                pic: 'http://t1.market.xiaomi.com/download/Browser/0968a4cb9e14a538cccc1eeb85df8ce16d3425bbb/thumb.jpg',
                url: location.href,
            });
        },
        getMoreReport() {
            this.$o2o({
                e_c: `${this.o2oChannel}肺炎频道`,
                e_a: '点击',
                e_n: `${this.o2oChannel}查看更多报道点击`,
            });
            this.goAppStore();
        },
        clickMoreEpidemicTitle() {
            this.$o2o({
                e_c: `${this.o2oChannel}肺炎频道`,
                e_a: '点击',
                e_n: `${this.o2oChannel}更多疫情按钮点击`,
            });
            this.goAppStore();
        },
        clickMoreEpidemicBanner() {
            this.$o2o({
                e_c: `${this.o2oChannel}肺炎频道`,
                e_a: '点击',
                e_n: `${this.o2oChannel}前往想看按钮点击`,
            });
            this.goAppStore();
        },
        goAppStore() {
            deeplinkApp(getTabLink('newsTab?switchChannelCode=抗击肺炎'), () => {}, 'CK1451326857236');
        },
        computeParams(item) {
            return {
                url: item.url,
                title: item.title,
                images: item.images,
                authorName: item.author_name,
                source: item.source,
                itemStyle: item.item_style,
                publishTime: item.publishTime,
                commentCount: item.commentCount,
                docid: item.docid,
            };
        },
    },
};
</script>

<style scoped>
@import url(./css/index.css);
</style>
