<template>
    <div class="conv-map">
        <div class="conv-title">
            <div class="title">
                疫情地图
            </div>
        </div>
        <div
            class="map"
            @click="goAppStore('map')">
            <img
                @error="imgDownloadError"
                :src="data.mapLink">
        </div>
        <div class="footer">以上数据来自夸克浏览器</div>
    </div>
</template>

<script>
import shareChannel from './config';

export default {
    name: 'ConvMap',
    props: {
        data: {
            type: Object,
            required: true,
        },
    },
    methods: {
        goAppStore() {
            const { o2oChannel } = shareChannel.call(this);
            this.$o2o({
                e_c: `${o2oChannel}肺炎频道`,
                e_a: '点击',
                e_n: `${o2oChannel}肺炎地图点击`,
            });
            this.$emit('goAppStore');
        },
        imgDownloadError(e) {
            e.target.src = 'http://t1.market.xiaomi.com/download/Browser/079c6d4cb67214c05289b7ceef95055529bdb6c27/map.jpg';
        },
    },
};
</script>

