<template>
    <div class="current">
        <div class="line Hairline Hairline_top"/>
        <div class="title">
            实时疫情<span>数据统计截止 {{ data.time }}</span>
        </div>
        <div class="table">
            <div class="table-tr table-head">
                <div class="table-td">地区</div>
                <div class="table-td">确诊</div>
                <div class="table-td">治愈</div>
                <div class="table-td">死亡</div>
            </div>
            <div
                v-if="total"
                class="table-tr country">
                <div class="table-td">全国</div>
                <div class="table-td">{{ total.sure_cnt }}</div>
                <div class="table-td">{{ total.cure_cnt }}</div>
                <div class="table-td">{{ total.die_cnt }}</div>
            </div>
            <div
                v-if="yst"
                class="table-tr added Hairline Hairline_bottom">
                <div class="table-td">{{ getDate(data.incrTime) }}号新增</div>
                <div class="table-td"><span>+{{ yst.sure_cnt }}</span></div>
                <div class="table-td"><span>+{{ yst.cure_cnt }}</span></div>
                <div class="table-td"><span>+{{ yst.die_cnt }}</span></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Current',
    props: {
        data: {
            type: Object,
            required: true,
        },
    },
    computed: {
        total() {
            return this.data.total;
        },
        yst() {
            return this.data.yst;
        },
    },
    methods: {
        getDate(timestamp) {
            const time = new Date(timestamp * 1000);

            return time.getDate();
        },
    },
};
</script>

