<template>
    <div
        @click="goDetail"
        class="item-container">
        <div
            v-if="parseInt(item.itemStyle) === 3"
            class="item-mult-image">
            <div
                v-if='item.title'
                class="item-mult-name">
                {{ item.title }}
            </div>
            <div class="item-mult-preview">
                <img
                    class="item-mult-preview-image"
                    v-for="(value,key) in item.images.slice(0, 3)"
                    :key='key'
                    :src="value"
                    alt="">
            </div>
            <ItemInfo
                id='item-info-mult'
                :item='item'/>
        </div>
        <div
            v-else-if='parseInt(item.itemStyle) === 1 && item.images[0]'
            class="item-single">
            <div
                v-if='item.title'
                class="item-name">
                {{ item.title }}
                <ItemInfo
                    class='item-info'
                    :item='item'/>
            </div>
            <img
                class="item-preview"
                v-if="(item.images && item.images.length)"
                :src="item.images[0]"
                alt="">

        </div>
        <div
            v-else-if='parseInt(item.itemStyle)=== 0
            ||(parseInt(item.itemStyle) === 1 && !item.images[0])'
            class="item-empty">
            <div
                v-if='item.title'
                class="item-name-empty">
                {{ item.title }}
            </div>
            <ItemInfo
                id='item-info-empty'
                :item='item'/>
        </div>
        <div
            v-else
            class="single-video">
            <div
                v-if='item.title'
                class="video-name">
                {{ item.title }}
            </div>
            <div>
                <img
                    class="video-preview"
                    v-if="(item.images && item.images.length)"
                    :src="item.images[0]"
                    alt="">
                <span class="video-play-icon"/>
            </div>
            <ItemInfo
                :item='item'
                id="video-item-info"/>
        </div>
    </div>
</template>

<script>
import ItemInfo from './ItemInfo';

export default {
    name: 'ListItem',
    props: {
        item: {
            type: Object,
            required: true,
        },
        pathname: {
            type: String,
            required: true,
        },
        xiaomiShequ: {
            type: Boolean,
            required: true,
        },
        o2oChannel: {
            type: String,
            required: true,
        },
    },
    components: {
        ItemInfo,
    },
    methods: {
        goDetail() {
            // 小米社区中 文章详情页调起想看app
            if (this.xiaomiShequ) {
                this.$emit('goAppStore');
                this.$o2o({
                    e_c: `${this.o2oChannel}肺炎频道内容`,
                    e_a: '点击',
                    e_n: `${this.o2oChannel}肺炎内容点击`,
                });
                return;
            }

            this.$root.setBackflow(this.pathname);
            location.href = this.item.url.includes('?') ? `${this.item.url}&docid=${this.item.docid}` : `${this.item.url}?docid=${this.item.docid}`;
            this.$o2o({
                e_c: '微信肺炎频道内容',
                e_a: '点击',
                e_n: '微信肺炎内容点击',
            });
        },
    },
};
</script>

<style>
@import url(./css/Item.css);
</style>
